import Canvas from "../../../Canvas";
import Vec2 from "vec2";
import { useEffect, useRef, useState } from "react";
import { Button, Card, Checkbox, Form, InputNumber } from "antd";
import "./index.css";

const modalConfigure = (onFinish, fields = [], initialValues = {}) => {
  const items = [];
  fields.forEach((field) => {
    const { label, name, type } = field;
    items.push(
      <>
        {type === "boolean" && (
          <Form.Item name={name} valuePropName="checked">
            <Checkbox>{label}</Checkbox>
          </Form.Item>
        )}
        {type === "number" && (
          <Form.Item label={label} name={name}>
            <InputNumber controls={false} style={{ width: "100%" }} />
          </Form.Item>
        )}
      </>
    );
  });

  return (
    <>
      <Card
        bordered={false}
        style={{
          position: "absolute",
          bottom: 150,
          left: 200,
          zIndex: 10,
          backgroundColor: "white",
          width: 300,
          borderRadius: 0,
        }}
      >
        <Form
          onFinish={onFinish}
          autoComplete="off"
          layout="vertical"
          initialValues={initialValues}
        >
          {[items]}
          <Button type="primary" htmlType="submit">
            OK
          </Button>
        </Form>
      </Card>
      <Card
        bordered={false}
        style={{
          position: "absolute",
          bottom: 200,
          left: 700,
          zIndex: 9,
          backgroundColor: "white",
          width: 600,
          borderRadius: 0,
        }}
      ></Card>
    </>
  );
};

class Ball {
  acceleration = Vec2(0, 0);
  constructor(x, y, size, color) {
    this.positionOld = Vec2(x - 1, y);
    this.positionCurrent = Vec2(x, y);
    this.size = size;
    this.color = color;
  }
}

export default function InelasticCollisionBalls() {
  const [balls, setBalls] = useState([]);
  const [ballsQuantity, setBallsQuantity] = useState(600);
  const [ballsSize, setBallsSize] = useState(15);
  const [gravity, setGravity] = useState(Vec2(0, 9.8));
  const [pullForce, setPullForce] = useState(1000);
  const width = document.documentElement.clientWidth;
  const height = document.documentElement.clientHeight;
  const intervalRef = useRef(null);
  const [items, setItems] = useState();

  function init(ballsQ) {
    const ballsArray = [];
    for (let i = 0; i < ballsQ; i++) {
      const color = `hsl(${Math.round(Math.random() * 360)}, 100%, 50%)`;
      ballsArray.push(
        new Ball(
          ballsSize + Math.random() * (width - ballsSize * 2),
          ballsSize + Math.random() * (height - ballsSize * 2),
          Math.random() * ballsSize + ballsSize / 2,
          // Math.random() * ballsSize + 1,
          color
        )
      );
    }
    return ballsArray;
  }

  useEffect(() => {
    if (ballsQuantity) {
      setBalls(init(ballsQuantity));
    }
  }, [ballsQuantity, ballsSize]);

  function frame(ctx) {
    // for (const ball of balls) {
    //   ball.size += 2 - Math.random() * 4.1;
    //   if (ball.size < 0) ball.size = 0;
    // }
    applyGravity();
    applyConstraint();
    solveCollisions();
    applyUpdate();
    applyDraw(ctx);
  }

  function update(ball, dt = 0.1) {
    const vel = ball.positionCurrent.subtract(ball.positionOld, true);
    ball.positionOld = ball.positionCurrent.clone();
    ball.positionCurrent.add(
      vel.add(ball.acceleration.multiply(dt ** 2, true), true)
    );
    ball.color = `hsl(${
      (parseInt(ball.positionCurrent.y / 100) * 360) / (height / 100)
    }, 100%, 50%)`;
    // ball.color = `hsl(${(ball.positionCurrent.y * 360) / height}, 100%, 50%)`;
    ball.acceleration = Vec2(0, 0);
  }

  function accelerate(ball, acc) {
    ball.acceleration.add(acc);
  }

  function applyDraw(ctx) {
    for (const ball of balls) {
      ctx.fillStyle = ball.color;
      ctx.beginPath();
      ctx.arc(
        ball.positionCurrent.x,
        ball.positionCurrent.y,
        ball.size,
        0,
        Math.PI * 2
      );
      ctx.closePath();
      ctx.fill();
    }
  }
  function applyUpdate() {
    for (const ball of balls) {
      update(ball);
    }
  }
  function applyGravity() {
    for (const ball of balls) {
      accelerate(ball, gravity);
    }
  }
  function applyConstraint() {
    function checkCornerCollision(ball, corner) {
      const vectToBall = ball.positionCurrent.subtract(corner, true);
      if (vectToBall.length() < ball.size) {
        const unitVectToBall = vectToBall.normalize(true);
        const vectToBallNeeded = unitVectToBall.multiply(ball.size, true);
        ball.positionCurrent = corner.add(vectToBallNeeded, true);
      }
    }
    function checkElementCollision(ball, elem) {
      if (
        elem.right > ball.positionCurrent.x - ball.size &&
        elem.left < ball.positionCurrent.x + ball.size &&
        elem.bottom > ball.positionCurrent.y - ball.size &&
        elem.top < ball.positionCurrent.y + ball.size
      ) {
        let shortestDiff = Infinity;
        const final = { prop: "x", val: ball.positionCurrent.x };
        const rightDiff = elem.right - ball.positionCurrent.x;
        let checkCorner = false;
        let checkCornerFn = () => {};

        if (rightDiff < shortestDiff) {
          checkCorner = false;
          if (elem.bottom < ball.positionCurrent.y) {
            checkCorner = true;
            checkCornerFn = () =>
              checkCornerCollision(ball, Vec2(elem.right, elem.bottom));
          } else if (elem.top > ball.positionCurrent.y) {
            checkCorner = true;
            checkCornerFn = () =>
              checkCornerCollision(ball, Vec2(elem.right, elem.top));
          } else {
            final.prop = "x";
            final.val = elem.right + ball.size;
          }
          shortestDiff = rightDiff;
        }

        const leftDiff = ball.positionCurrent.x - elem.left;
        if (leftDiff < shortestDiff) {
          checkCorner = false;
          if (elem.bottom < ball.positionCurrent.y) {
            checkCorner = true;
            checkCornerFn = () =>
              checkCornerCollision(ball, Vec2(elem.left, elem.bottom));
          } else if (elem.top > ball.positionCurrent.y) {
            checkCorner = true;
            checkCornerFn = () =>
              checkCornerCollision(ball, Vec2(elem.left, elem.top));
          } else {
            final.prop = "x";
            final.val = elem.left - ball.size;
          }
          shortestDiff = leftDiff;
        }

        const bottomDiff = elem.bottom - ball.positionCurrent.y;
        if (bottomDiff < shortestDiff) {
          checkCorner = false;
          if (elem.right < ball.positionCurrent.x) {
            checkCorner = true;
            checkCornerFn = () =>
              checkCornerCollision(ball, Vec2(elem.right, elem.bottom));
          } else if (elem.left > ball.positionCurrent.x) {
            checkCorner = true;
            checkCornerFn = () =>
              checkCornerCollision(ball, Vec2(elem.left, elem.bottom));
          } else {
            final.prop = "y";
            final.val = elem.bottom + ball.size;
          }
          shortestDiff = bottomDiff;
        }

        const topDiff = ball.positionCurrent.y - elem.top;
        if (topDiff < shortestDiff) {
          checkCorner = false;
          if (elem.right < ball.positionCurrent.x) {
            checkCorner = true;
            checkCornerFn = () =>
              checkCornerCollision(ball, Vec2(elem.right, elem.top));
          } else if (elem.left > ball.positionCurrent.x) {
            checkCorner = true;
            checkCornerFn = () =>
              checkCornerCollision(ball, Vec2(elem.left, elem.top));
          } else {
            final.prop = "y";
            final.val = elem.top - ball.size;
          }
          // shortestDiff = topDiff;
        }

        if (checkCorner) {
          checkCornerFn();
        } else {
          ball.positionCurrent[final.prop] = final.val;
        }
      }
    }
    for (const ball of balls) {
      if (ball.positionCurrent.y > height - ball.size) {
        ball.positionCurrent = Vec2(ball.positionCurrent.x, height - ball.size);
      }
      if (ball.positionCurrent.y < ball.size) {
        ball.positionCurrent = Vec2(ball.positionCurrent.x, ball.size);
      }
      if (ball.positionCurrent.x > width - ball.size) {
        ball.positionCurrent = Vec2(width - ball.size, ball.positionCurrent.y);
      }
      if (ball.positionCurrent.x < ball.size) {
        ball.positionCurrent = Vec2(ball.size, ball.positionCurrent.y);
      }

      if (items) {
        items.forEach((item) => checkElementCollision(ball, item));
      }
    }
  }
  function solveCollisions() {
    for (let i = 0; i < balls.length; i++) {
      const ball1 = balls[i];
      for (let k = i + 1; k < balls.length; k++) {
        const ball2 = balls[k];
        const collisionAxis = ball1.positionCurrent.subtract(
          ball2.positionCurrent,
          true
        );
        const dist = collisionAxis.length();
        if (dist < ball1.size + ball2.size) {
          const n = collisionAxis.divide(dist || 0.000001, true);
          const delta = ball1.size + ball2.size - dist;
          ball1.positionCurrent.add(n.multiply(0.5 * delta, true));
          ball2.positionCurrent.subtract(n.multiply(0.5 * delta, true));
        }
      }
    }
  }

  const paintEvent = (clickPos) => {
    const color = `hsl(${Math.round(Math.random() * 360)}, 100%, 50%)`;
    for (const ball of balls) {
      if (ball.positionCurrent.distance(clickPos) < 300) {
        ball.color = color;
      }
    }
  };

  const liftEvent = (clickPos) => {
    for (const ball of balls) {
      if (ball.positionCurrent.distance(clickPos) < 300) {
        ball.positionOld = ball.positionCurrent.add(Vec2(0, 10), true);
      }
    }
  };

  const pushEvent = (clickPos) => {
    for (const ball of balls) {
      const distV = clickPos.subtract(ball.positionCurrent, true);
      const distVVal = distV.length();
      const unitDistV = distV.normalize(true);
      const vectToMouse = unitDistV.multiply(1000 / distVVal ** 1, true);
      ball.positionOld = ball.positionOld.add(vectToMouse, true);
    }
  };

  const pullEvent = (clickPos) => {
    for (const ball of balls) {
      const distV = ball.positionCurrent.subtract(clickPos, true);
      const distVVal = distV.length();
      // ball.color = `hsl(${(distVVal * 360) / width}, 100%, 50%)`;
      // ball.color = `hsl(360, 100%, ${(distVVal ** 1.1 * 50) / width + 50}%)`;
      const unitDistV = distV.normalize(true);
      const vectToMouse = unitDistV.multiply(
        pullForce / (distVVal ** 1.6 > pullForce ? distVVal ** 1.6 : pullForce),
        true
      );
      ball.positionOld = ball.positionOld.add(vectToMouse, true);
    }
  };

  const onClick = (e) => {
    const clickPos = Vec2(e.nativeEvent.offsetX, e.nativeEvent.offsetY);
    paintEvent(clickPos);
    // liftEvent(clickPos);
    pushEvent(clickPos);
  };

  const onRightClick = (e) => {
    e.preventDefault();
    const clickPos = Vec2(e.nativeEvent.offsetX, e.nativeEvent.offsetY);
    paintEvent(clickPos);
    pullEvent(clickPos);
  };

  useEffect(() => {
    let elems = document.querySelectorAll("div.ant-card");
    const rects = [];
    elems.forEach((elem) => rects.push(elem.getBoundingClientRect()));
    setItems(rects);
    return () => stopCounter(); // when App is unmounted we should stop counter
  }, []);

  let mousePos;

  const startCounter = (e) => {
    e.preventDefault();
    if (intervalRef.current) return;
    intervalRef.current = setInterval(() => {
      const clickPos =
        mousePos || Vec2(e.nativeEvent.offsetX, e.nativeEvent.offsetY);
      pullEvent(clickPos);
    }, 10);
  };

  const stopCounter = () => {
    if (intervalRef.current) {
      clearInterval(intervalRef.current);
      intervalRef.current = null;
    }
  };

  const fields = [
    {
      label: "Num of balls",
      name: "num_balls",
      type: "number",
    },
    {
      label: "Size of balls",
      name: "balls_size",
      type: "number",
    },
    {
      label: "Force of pull",
      name: "pull_force",
      type: "number",
    },
    {
      label: "Has gravity",
      name: "has_gravity",
      type: "boolean",
    },
  ];

  const onFinish = (values) => {
    const { num_balls, balls_size, pull_force, has_gravity } = values;
    num_balls && setBallsQuantity(num_balls);
    balls_size && setBallsSize(balls_size);
    pull_force && setPullForce(pull_force);
    if (has_gravity !== undefined)
      setGravity(has_gravity ? Vec2(0, 9.8) : Vec2(0, 0));
  };

  const initialValues = {
    has_gravity: true,
    num_balls: ballsQuantity,
    balls_size: ballsSize,
    pull_force: pullForce,
  };
  const modal = modalConfigure(onFinish, fields, initialValues);

  return (
    <>
      {modal}
      <div
        style={{
          position: "absolute",
          height,
          width,
          backgroudColor: "red",
          pointerEvents: "none",
          zIndex: 1,
        }}
      ></div>
      <Canvas
        draw={frame}
        height={height}
        width={width}
        // onClick={onClick}
        // onRightClick={onRightClick}
        onMouseDown={startCounter}
        onMouseUp={stopCounter}
        onMouseMove={(e) =>
          (mousePos = {
            x: e.nativeEvent.offsetX,
            y: e.nativeEvent.offsetY,
          })
        }
      />
    </>
  );
}
