import PropTypes from "prop-types";
import { useEffect, useRef } from "react";

const Canvas = (props) => {
  const {
    draw,
    height,
    width,
    onClick,
    onRightClick,
    onMouseDown,
    onMouseUp,
    onMouseMove,
  } = props;
  const canvas = useRef();

  let animatedRunningCurrent = 0;
  useEffect(() => {
    const context = canvas.current.getContext("2d");
    canvas.current.width = width;
    canvas.current.height = height;
    animatedRunningCurrent++;
    const drawFunc = (ms, animatedRunning) => {
      if (animatedRunning === animatedRunningCurrent) {
        requestAnimationFrame((ms) => drawFunc(ms, animatedRunning));
        // setTimeout(drawFunc, 1000);
        context.fillStyle = "rgba(0,0,0,0.2)";
        context.fillRect(0, 0, width, height);
        draw(context, ms);
      }
    };
    drawFunc(0, animatedRunningCurrent);
    return () => {
      animatedRunningCurrent++;
    };
  });

  // const restart = () => {
  //   canvas.width = window.innerWidth;
  //   canvas.height = window.innerHeight;
  //   init();
  //   animatedRunningCurrent++;
  //   animate(animatedRunningCurrent);
  // };

  useEffect(() => {
    // Prevent right click popover
    window.addEventListener("contextmenu", (e) => e.preventDefault());
  });

  return (
    <canvas
      ref={canvas}
      height={height}
      width={width}
      onClick={onClick}
      onAuxClick={onRightClick}
      onMouseDown={onMouseDown}
      onMouseUp={onMouseUp}
      onMouseMove={onMouseMove}
    />
  );
};

Canvas.propTypes = {
  draw: PropTypes.func.isRequired,
  height: PropTypes.number.isRequired,
  width: PropTypes.number.isRequired,
};

export default Canvas;
