import { Outlet } from "react-router";
import ElasticCollisionBalls from "./components/2dCanvas/CollisionBalls/Elastic";
import InelasticCollisionBalls from "./components/2dCanvas/CollisionBalls/Inelastics";

export const routes = [
  {
    path: "/",
    element: (
      <>
        <Outlet />
      </>
    ),
    children: [
      { index: true, element: <InelasticCollisionBalls /> },
      {
        path: "/elastic",
        element: <ElasticCollisionBalls />,
      },
    ],
  },
];
